<script>
    import startTimer from "./timer.js";
    import {alarmUrl, clickUrl, smallclickUrl, unclickUrl} from "./audiourls.js";

    let hotReps, coldReps, upReps, downReps, inReps, outReps;
    let hotCounter = "",
        coldCounter = "",
        timerCancel = null,
        welcomed = false;

    let clickPlayer, unclickPlayer, smallclickPlayer, alarmPlayer;

    const hotTotal = 5,
        coldTotal = 5,
        upTotal = 10,
        downTotal = 10,
        inTotal = 10,
        outTotal = 10,
        hotColdTime = 60;

    const resetCounters = () => {
        hotReps = 0;
        coldReps = 0;
        upReps = 0;
        downReps = 0;
        inReps = 0;
        outReps = 0;
        if (typeof timerCancel === "function") {
            timerCancel();
            timerCancel = null;
        }
    };
    resetCounters();

    const handleTimer = (hot) => {
        if (typeof timerCancel === "function") {
            timerCancel();
            timerCancel = null;
            if (unclickPlayer) {
                unclickPlayer.load();
                unclickPlayer.play();
            }
        } else {
            timerCancel = startTimer(
                hotColdTime,
                (val) => {
                    if (hot) hotCounter = val;
                    else coldCounter = val;
                },
                (cancelled) => {
                    hotCounter = "";
                    coldCounter = "";
                    timerCancel = null;
                    if (!cancelled) {
                        if (hot) hotReps++;
                        else coldReps++;
                        if (alarmPlayer) {
                            alarmPlayer.load();
                            alarmPlayer.play();
                        }
                    }
                }
            );
            if (clickPlayer) {
                clickPlayer.load();
                clickPlayer.play();
            }
        }
    };

    const alarmClick = () => {
        if (alarmPlayer) {
            alarmPlayer.load();
            alarmPlayer.play();
        }
        welcomed = true;
    };

    const smallClick = () => {
        if (smallclickPlayer) {
            smallclickPlayer.load();
            smallclickPlayer.play();
        }
    };
</script>

<audio bind:this={clickPlayer} src={clickUrl} preload="auto"></audio>
<audio bind:this={unclickPlayer} src={unclickUrl} preload="auto"></audio>
<audio bind:this={smallclickPlayer} src={smallclickUrl} preload="auto"></audio>
<audio bind:this={alarmPlayer} src={alarmUrl} preload="auto"></audio>

{#if welcomed}
<main>
    <h1>Physio Tracker</h1>
    <section class="mb-1">
        <h2>Contrast Bath</h2>
        <div class="grid two">
            <button on:click={() => handleTimer(true)} class="warmer">
                <h3>Hot</h3>
                <span
                    >{`${hotReps} / ${hotTotal}${
                        hotCounter !== "" ? " - " + hotCounter : ""
                    }`}</span
                >
            </button>
            <button on:click={() => handleTimer(false)} class="colder">
                <h3>Cold</h3>
                <span
                    >{`${coldReps} / ${coldTotal}${
                        coldCounter !== "" ? " - " + coldCounter : ""
                    }`}</span
                >
            </button>
        </div>
    </section>
    <section class="mb-1">
        <h2>Towel Stretches</h2>
        <div class="grid four">
            <button on:click={() => {upReps++; smallClick();}} class="lighter">
                <h3>Up</h3>
                <span>{`${upReps} / ${upTotal}`}</span>
            </button>
            <button on:click={() => {downReps++; smallClick();}} class="lighter">
                <h3>Down</h3>
                <span>{`${downReps} / ${downTotal}`}</span>
            </button>
            <button on:click={() => {inReps++; smallClick();}} class="lighter">
                <h3>Inwards</h3>
                <span>{`${inReps} / ${inTotal}`}</span>
            </button>
            <button on:click={() => {outReps++; smallClick();}} class="lighter">
                <h3>Outwards</h3>
                <span>{`${outReps} / ${outTotal}`}</span>
            </button>
        </div>
    </section>
    <button on:click={() => {resetCounters(); smallClick();}}><span>Reset all counters</span></button>
</main>
{:else}
<main>
    <h1>Welcome</h1>
    <p>This application's purpose is to help keep track of the simple physiotherapy exercises for the recovery of an ankle injury.</p>
    <p>Click on the various buttons to increase the number of completed repetitions for the corresponding exercise.</p>
    <p>For the Hot/Cold contrast bath, a timer will guide you and automatically increase your repetition.</p>
    <button on:click={alarmClick}><span>Let's get started</span></button>
</main>
{/if}

<style>
    h1, p {
        color: #ffffff;
    }

    h2,
    h3 {
        margin: 0;
        margin-bottom: 1rem;
    }

    main {
        width: 100vw;
        max-width: min(425px, 100vw);
        display: block;
        padding: 1rem;
        margin: 0 auto;
    }

    section,
    button {
        width: 100%;
        background-color: #777788;
        color: #ebebff;
        padding: 1rem;
        margin: 0;
        border: none;
        border-radius: 1rem;
        text-align: left;
        touch-action: manipulation;
        font-size: inherit;
    }

    section span,
    button span {
        font-weight: bold;
    }

    button.lighter {
        background-color: #ebebff;
        color: #777788;
    }

    button.warmer {
        background-color: #f3a48a;
        color: #fef4f1;
    }

    button.colder {
        background-color: #8ac0f3;
        color: #f1fcfe;
    }

    button:active {
        background-color: #53535f;
    }

    button.lighter:active {
        background-color: #fafaff;
    }

    button.warmer:active {
        background-color: #e95e2f;
    }

    button.colder:active {
        background-color: #2f8fe9;
    }

    .grid {
        display: grid;
        gap: 1rem;
    }

    .grid.two {
        grid-template-rows: 1fr;
        grid-template-columns: 1fr 1fr;
    }

    .grid.four {
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr 1fr;
    }

    .mb-1 {
        margin-bottom: 1rem;
    }
</style>
